.connect-links {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: 0;
  margin-bottom: 5vh;
  font-family: Courier New, Courier, monospace;
  font-size: 1.25em;
  display: flex;
}

.connect-links img {
  height: 3vh;
  margin-right: 10px;
}

.linkedin-link {
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 15px;
  display: flex;
}

.github-link {
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.links-container {
  background: linear-gradient(90deg, #000 50%, #0000 50%) 0 0 / 20px 1px repeat-x, linear-gradient(90deg, #000 50%, #0000 50%) 100% 100% / 20px 1px repeat-x, linear-gradient(0deg, #000 50%, #0000 50%) 0 100% / 1px 20px repeat-y, #fff linear-gradient(0deg, #000 50%, #0000 50%) 100% 0 / 1px 20px repeat-y;
  width: 50%;
  height: 100%;
  padding: 20px;
  transition: box-shadow .3s;
  animation: 7s linear infinite dash;
  box-shadow: 5px 5px #87878780;
}

.links-container:hover {
  box-shadow: 5px 5px #6d006db3;
}

@keyframes dash {
  to {
    background-position: 100% 0, 0 100%, 0 0, 100% 100%;
  }
}

@media screen and (width <= 768px) {
  .links-container {
    width: 95%;
  }
}
